import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class TextFieldHint extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			show,
			className,
			text
		} = props;

		const klassName = KlassName('field-hint',className);

		return (
			<div
				className={klassName}
				style={{display: show ? 'block' : 'none',opacity: show ? 0.8 : 0}}
			>
				{text}
			</div>
		);
	}
};

TextFieldHint.propTypes = {
	className: PropTypes.string,
	text: PropTypes.node,
	show: PropTypes.bool,
};

TextFieldHint.defaultProps = {
	show: true,
};

export default TextFieldHint;